<template>
	<div>
		<b-btn variant="success" style="position: absolute;
    top: 5px;
    right: 5px;" @click="doStuff">submit</b-btn>

    <b-btn @click="validate" variant="primary" type="submit" style="position: absolute;
    top: 5px;
    right: 85px;">Validate</b-btn>
	</div>
</template>

<script>

import {saveInstance} from '../api.js';

export default {

  name: 'FormSubmit',


  props: {
  	info: null,
  },

  updated() {
  },

  created() {
    this.validate_data = this.info
    
  },

  data () {
    return {
      validate_data: null,
    }
  },

  methods: {
  	doStuff(){
  		var info = this.info
      console.log(info)
      saveInstance(info)
  	},
    validate() {
      let data = this.validate_data;
      delete data.tab_1
      let validate_data = [];

        for(let a in data) {
          var tab_title = data[a].label
          for(let b of data[a].data.articles) {
            var article_title = b.article_title
            console.log(article_title)
          }
        }

    }
  },
}
</script>

<style lang="css" scoped>
</style>


